
import { defineComponent, ref } from 'vue'
import { Toast } from 'vant'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'Search',
  components: {
  },
  setup () {
    const router = useRouter()
    const goBack = () => {
      router.back()
    }

    const searchVal = ref('')
    const onSearch = (val: string) => Toast(val)
    return {
      goBack,
      searchVal,
      onSearch
    }
  }
})
