<template>
  <van-nav-bar fixed left-arrow :title="title" @click-left="goBack" class="nav"></van-nav-bar>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { useRouter } from 'vue-router'

export default defineComponent({
  props: {
    title: {
      type: String
    }
  },
  setup () {
    const router = useRouter()
    const goBack = () => {
      router.back()
    }
    return {
      goBack
    }
  }
})
</script>

<style lang="stylus" scoped>
.nav
  --van-nav-bar-icon-color var(--ot-button-color)
</style>
