
import { computed, defineComponent, ref, watch } from 'vue'
import TheNavBar from '../components/TheNavBar.vue'
import { routeNamesEnum, routePathsEnum, topViewsEnum } from '../router/routerEnum'
import { useRoute } from 'vue-router'
import ShareRecommendation from './ShareRecommendation.vue'
import ShareAttention from './ShareAttention.vue'

export default defineComponent({
  components: {
    TheNavBar,
    ShareRecommendation,
    ShareAttention
  },
  setup () {
    const recommendationPath = routePathsEnum.Share
    const attentionPath = `${routePathsEnum.Share}/${routePathsEnum.ShareAttention}`

    const getActiveComponentName = (path: string): string => {
      let activeName = ''
      if (path === recommendationPath) {
        activeName = ShareRecommendation.name
      } else if (path === attentionPath) {
        activeName = ShareAttention.name
      }
      return activeName
    }

    const route = useRoute()
    const activeComponentName = ref(getActiveComponentName(route.fullPath))
    // console.log('activeComponentName:', activeComponentName.value)

    watch(
      () => route.params,
      () => {
        activeComponentName.value = getActiveComponentName(route.fullPath)
        // console.log('watch activeComponentName:', activeComponentName.value)
      }
    )
    return {
      tabs: [
        {
          name: routeNamesEnum.ShareAttention, title: '关注', path: attentionPath
        },
        {
          name: routeNamesEnum.Share, title: '推荐', path: recommendationPath
        }
      ],
      viewType: topViewsEnum.Share,
      activeComponentName
    }
  }
})
