
import { computed, defineComponent, PropType } from 'vue'
import { ImageTextCardProps } from '../store/store'
import WrapLayout from '../layouts/wrapLayout.vue'
import VCard from './VCard.vue'

export default defineComponent({
  name: 'ImageTextCardList',
  components: {
    WrapLayout,
    VCard
  },
  props: {
    list: {
      type: Array as PropType<ImageTextCardProps[]>,
      required: true
    }
  },
  setup (props) {
    const cardList = computed(() => {
      return props.list
      // return props.list.map(chartCard => chartCard)
    })
    return {
      cardList
    }
  }
})
