import { createApp, App } from 'vue'
import RootApp from './App.vue'
import router from './router/router'
import { ConfigProvider, Button, Icon, Col, Row, NavBar, Tag, Tabs, Tab, Tabbar, TabbarItem, Collapse, CollapseItem, DropdownMenu, DropdownItem, Image, Search } from 'vant'
import 'normalize.css/normalize.css'
// vxe table
import XEUtils from 'xe-utils'
import {
  // 核心
  VXETable,

  // 表格功能
  Header,
  // Footer,
  // Icon,
  // Filter,
  // Edit,
  // Menu,
  // Export,
  // Keyboard,
  // Validator,

  // 可选组件
  Column,
  // Colgroup,
  // Grid,
  // Tooltip,
  // Toolbar,
  // Pager,
  // Form,
  // FormItem,
  // FormGather,
  // Checkbox,
  // CheckboxGroup,
  // Radio,
  // RadioGroup,
  // RadioButton,
  // Switch,
  // Input,
  // Select,
  // Optgroup,
  // Option,
  // Textarea,
  // Button,
  // Modal,
  // List,
  // Pulldown,

  // 表格
  Table
} from 'vxe-table'
import zhCN from 'vxe-table/es/locale/lang/zh-CN'
import store, { storeKey } from './store/store'

const app = createApp(RootApp)

function useVant (app: App) {
  app.use(ConfigProvider)
    // .use(Button)
    .use(Icon)
    .use(NavBar)
    .use(Tag)
    .use(Tabs)
    .use(Tab)
    .use(Tabbar)
    .use(TabbarItem)
    .use(Collapse)
    .use(CollapseItem)
    .use(DropdownMenu)
    .use(DropdownItem)
    .use(Image)
    .use(Search)
}

// 按需加载的方式默认是不带国际化的，自定义国际化需要自行解析占位符 '{0}'，例如：
VXETable.setup({
  i18n: (key, args) => XEUtils.toFormatString(XEUtils.get(zhCN, key), args)
})
function useTable (app: App) {
  // 表格功能
  app.use(Header)
  // .use(Footer)
  // .use(Icon)
  // .use(Filter)
  // .use(Edit)
  // .use(Menu)
  // .use(Export)
  // .use(Keyboard)
  // .use(Validator)

  // 可选组件
    .use(Column)
    // .use(Colgroup)
    // .use(Grid)
    // .use(Tooltip)
    // .use(Toolbar)
    // .use(Pager)
    // .use(Form)
    // .use(FormItem)
    // .use(FormGather)
    // .use(Checkbox)
    // .use(CheckboxGroup)
    // .use(Radio)
    // .use(RadioGroup)
    // .use(RadioButton)
    // .use(Switch)
    // .use(Input)
    // .use(Select)
    // .use(Optgroup)
    // .use(Option)
    // .use(Textarea)
    // .use(Button)
  // .use(Modal)
  // .use(List)
  // .use(Pulldown)

  // 安装表格
    .use(Table)

  // 给 vue 实例挂载内部对象，例如：
  // app.config.globalProperties.$XModal = VXETable.modal
  // app.config.globalProperties.$XPrint = VXETable.print
  // app.config.globalProperties.$XSaveFile = VXETable.saveFile
  // app.config.globalProperties.$XReadFile = VXETable.readFile
}

/* TODO: 更改组件的注册方式，局部注册方便 SSR 以及分路由加载策略 */
app
  .use(router)
  .use(store, storeKey)
  .use(useVant)
  .use(useTable)
  .mount('#app')
