
import { computed, defineComponent, ref } from 'vue'
import { reviewData } from '../mocks/localTest'
import FigureCardList from '../components/FigureCardList.vue'
import VCard from '../components/VCard.vue'
import ProsConsList from '../components/ProsConsList.vue'
import echarts, { createChartOption } from '../hooks/useCreateChart'
import WrapLayout from '../layouts/wrapLayout.vue'
import TheSimpleNavBar from '../components/TheSimpleNavBar.vue'

export default defineComponent({
  name: 'Review',
  components: {
    WrapLayout,
    VCard,
    FigureCardList,
    ProsConsList,
    TheSimpleNavBar
  },
  setup () {
    const baseInfo = computed(() => {
      return reviewData.baseInfo
    })

    const activeNames = ref([0])
    const evalInfo = computed(() => {
      return reviewData.eval
    })

    const adviceInfo = computed(() => {
      return reviewData.advice
    })

    let priceChart: echarts.ECharts | null = null
    const sizeState = ref(0)
    const sizeOption = reviewData.price.sizeOption

    const onDropdownChange = (value: number) => {
      console.log('value:', value)
      const option = createChartOption(reviewData.price.chartSource[value])
      priceChart && priceChart.setOption(option)
    }

    const onRendered = (index: number) => {
      if (index === 2) {
        const option = createChartOption(reviewData.price.chartSource[0])
        priceChart = echarts.init(document.getElementById('price-chart') as HTMLElement, undefined, { renderer: 'svg' })
        priceChart.setOption(option)
      }
    }

    const tableList = computed(() => reviewData.price.tableList)

    const figureGroupList = computed(() => reviewData.details.figureGroupList)
    return {
      baseInfo,
      evalInfo,
      activeNames,
      adviceInfo,
      sizeState,
      sizeOption,
      onDropdownChange,
      onRendered,
      tableList,
      figureGroupList
    }
  }
})
