import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import Seek from '@/views/Seek.vue'
import Share from '@/views/Share.vue'
import Search from '@/views/Search.vue'
import Top from '@/views/Top.vue'
import Review from '@/views/Review.vue'
import TheTabbar from '@/components/TheTabbar.vue'
import { routeNamesEnum, routePathsEnum } from './routerEnum'

// 底部导航首页激活包含的 path
export const homePaths: string[] = [
  routePathsEnum.Home,
  `${routePathsEnum.Home}/${routePathsEnum.HomePhone}`
]
// 底部导航首页激活包含的 path
export const sharePaths: string[] = [
  routePathsEnum.Share,
  `${routePathsEnum.Share}/${routePathsEnum.ShareAttention}`
  // `${routePathsEnum.Share}/${routePathsEnum.ShareRecommendation}`
]

const routerHistory = createWebHistory()
const router = createRouter({
  history: routerHistory,
  routes: [
    {
      path: '/',
      redirect: 'home'
    },
    {
      path: routePathsEnum.Home,
      name: routeNamesEnum.Home,
      components: {
        default: Home,
        bottomTabbar: TheTabbar
      },
      children: [
        {
          path: routePathsEnum.HomePhone,
          name: routeNamesEnum.HomePhone,
          components: {
            default: Home,
            bottomTabbar: TheTabbar
          }
        }
      ]
    },
    {
      path: routePathsEnum.Seek,
      name: routeNamesEnum.Seek,
      components: {
        default: Seek
      }
    },
    {
      path: routePathsEnum.Share,
      name: routeNamesEnum.Share,
      components: {
        default: Share,
        bottomTabbar: TheTabbar
        // shareContent: ShareRecommendation
      },
      children: [
        {
          path: routePathsEnum.ShareAttention,
          name: routeNamesEnum.ShareAttention,
          components: {
            default: Share,
            bottomTabbar: TheTabbar
            // shareContent: ShareAttention
          }
        }
      ]
    },
    {
      path: routePathsEnum.Search,
      name: routeNamesEnum.Search,
      component: Search
    },
    {
      path: `${routePathsEnum.Top}/:name`,
      name: routeNamesEnum.Top,
      component: Top
    },
    {
      path: `${routePathsEnum.Review}/:model`,
      name: routeNamesEnum.Review,
      component: Review
    }
  ]
})

export default router
