
import { defineComponent, inject, onMounted, reactive, ref } from 'vue'
import WaterFall from '../components/waterFall/water-fall.vue'
import { WaterfallProps } from '../store/store'
import WrapLayout from '../layouts/wrapLayout.vue'
import { shareData } from '../mocks/localTest'
import VCard from '../components/VCard.vue'
import { deviceWidthKey, globalStyleKey } from '../symbols/global'

export default defineComponent({
  name: 'ShareRecommendation',
  components: {
    WaterFall,
    WrapLayout,
    VCard
  },
  setup () {
    // 瀑布流相关
    const deviceWidth = inject(deviceWidthKey)
    if (deviceWidth === undefined) {
      console.log(deviceWidth)
      throw new Error('Failed to inject "deviceWidth"')
    }
    const globalStyle = inject(globalStyleKey)
    if (globalStyle === undefined) {
      throw new Error('Failed to inject "globalStyle"')
    }

    const { rowPadding } = globalStyle

    const waterfallItemWidth = ref<number | undefined>(0)
    const gap = 8 // px
    // console.log('deviceWidth:', deviceWidth)
    // 瀑布流距离两边的距离要缩小一点
    const recommendationRowPadding = rowPadding - 8
    waterfallItemWidth.value = Math.floor((deviceWidth - recommendationRowPadding * 2 - gap) / 2)
    // console.log('waterfallItemWidth:', waterfallItemWidth.value)
    const wrapRowPadding = recommendationRowPadding - gap / 2

    const payload = {
      page: 1,
      size: 20
    }

    let total = 0
    const tvData = ref<WaterfallProps[]>([])
    const phoneData = ref<WaterfallProps[]>([])

    function fetchData () {
      const { page, size } = payload
      // const prev = (page - 1) * size

      const data = shareData.waterFall(page, size, false)
      const data2 = shareData.waterFall(page, size, true)
      total = 500
      tvData.value = [...tvData.value, ...data]
      phoneData.value = [...phoneData.value, ...data2]
    }

    // 产品标签
    const activeProduct = ref('tv')
    const firstRender = reactive<{ tv: number; phone: number; }>({
      tv: 0,
      phone: 0
    })

    // 控制加载
    const onClickProduct = (tab: any) => {
      switch (tab.name) {
        case 'tv':
          firstRender.tv = 1
          break
        case 'phone':
          firstRender.phone = 1
          break
        default:
          break
      }
    }
    // topNav
    onMounted(() => {
      fetchData()
    })
    return {
      activeProduct,
      gap,
      wrapRowPadding,
      tvData,
      phoneData,
      waterfallItemWidth,
      tabStyle: {
        fontSize: '14px', display: 'inline', flex: 'none'
      },
      firstRender,
      onClickProduct
    }
  }
})
