<template>
  <main id="search">
    <section class="global-nav-bar">
      <van-search v-model="searchVal" placeholder="请输入关键词" @search="onSearch" class="search-input">
        <template #left>
          <div class="back" @click="goBack">
            <van-icon name="arrow-left" class="arrow-left" />
          </div>
        </template>
      </van-search>
    </section>
    <section class="global-cont"></section>
  </main>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import { Toast } from 'vant'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'Search',
  components: {
  },
  setup () {
    const router = useRouter()
    const goBack = () => {
      router.back()
    }

    const searchVal = ref('')
    const onSearch = (val: string) => Toast(val)
    return {
      goBack,
      searchVal,
      onSearch
    }
  }
})
</script>

<style lang="stylus" scoped>
