  padding var(--van-padding-xs)
.back
  display flex
  height 34px
  align-items center
  padding 0 var(--van-padding-sm) 0 var(--van-padding-base)
  font-size var(--van-font-size-md)
  cursor pointer
  .arrow-left
    font-size var(--van-nav-bar-arrow-size)
</style>
