<template>
  <main id="review">
    <section class="global-nav-bar">
      <the-simple-nav-bar></the-simple-nav-bar>
    </section>
    <section class="global-cont">
      <wrap-layout>
        <article>
          <h1>{{ baseInfo.title }}</h1>
          <time :datetime="baseInfo.date" class="date">{{ baseInfo.date }}</time>
          <figure><img :src="baseInfo.banner.url" :alt="baseInfo.title"></figure>
        </article>
      </wrap-layout>
      <van-tabs class="tabs" @rendered="onRendered">
        <van-tab title="综合评价">
          <wrap-layout class="eval">
            <p class="intro">{{ evalInfo.intro }}</p>
            <v-card :captions="[evalInfo.captions[0]]">
              <template #header>
                <div class="header">
                  <van-tag size="large" color="#43cf7c" class="tag">{{ evalInfo.score }}</van-tag>
                  <span>{{ evalInfo.reviewOption }}</span>
                </div>
              </template>
              <template #explain-bottom>
                <pros-cons-list :pros="evalInfo.pros" :cons="evalInfo.cons"></pros-cons-list>
              </template>
            </v-card>
          </wrap-layout>
        </van-tab>
        <van-tab title="使用建议">
          <wrap-layout class="advice">
            <van-collapse v-model="activeNames" class="collapse">
              <van-collapse-item v-for="(advice, index) in adviceInfo" :key="'key' + index" :name="index">
                <template #title>
                  <div class="collapse-title">
                    <van-tag size="large" color="#43cf7c" class="tag">{{ advice.score }}</van-tag>
                    <span>{{ advice.reviewOption }}</span>
                  </div>
                </template>
                <p class="intro">{{ advice.intro }}</p>
                <pros-cons-list :pros="advice.pros" :cons="advice.cons"></pros-cons-list>
              </van-collapse-item>
            </van-collapse>
          </wrap-layout>
        </van-tab>
        <van-tab title="价格趋势">
          <wrap-layout class="price">
            <div class="dropdown-wrap">
              <van-dropdown-menu>
                <van-dropdown-item v-model="sizeState" :options="sizeOption" class="dropdown-item" @change="onDropdownChange" />
              </van-dropdown-menu>
            </div>
            <div id="price-chart" :style="{ width: '100%', height: '300px' }"></div>
            <vxe-table class="price-table" size="mini" align="center" :data="tableList">
              <vxe-table-column field="model" title="尺寸/型号">
                <template #default="{ row }">
                  <ul>
                    <li class="size">{{ row.model.size }}</li>
                    <li class="name">{{ row.model.name }}</li>
                  </ul>
                </template>
              </vxe-table-column>
              <vxe-table-column field="jdPrice" title="京东">
                <template #default="{ row }">
                  <a href="javascript:;" class="price-val">{{ row.jdPrice }}</a>
                </template>
              </vxe-table-column>
              <vxe-table-column field="tbPrice" title="淘宝">
                <template #default="{ row }">
                  <a href="javascript:;" class="price-val">{{ row.jdPrice }}</a>
                </template>
              </vxe-table-column>
              <vxe-table-column field="pddPrice" title="拼多多">
                <template #default="{ row }">
                  <a href="javascript:;" class="price-val">{{ row.jdPrice }}</a>
                </template>
              </vxe-table-column>
            </vxe-table>
          </wrap-layout>
        </van-tab>
        <van-tab title="评测详情">
          <figure-card-list v-for="(figureGroup, index) in figureGroupList" :key="'fi' + index" :category="figureGroup.category" :list="figureGroup.figureCardList"></figure-card-list>
        </van-tab>
      </van-tabs>
    </section>
  </main>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from 'vue'
import { reviewData } from '../mocks/localTest'
import FigureCardList from '../components/FigureCardList.vue'
import VCard from '../components/VCard.vue'
import ProsConsList from '../components/ProsConsList.vue'
import echarts, { createChartOption } from '../hooks/useCreateChart'
import WrapLayout from '../layouts/wrapLayout.vue'
import TheSimpleNavBar from '../components/TheSimpleNavBar.vue'

export default defineComponent({
  name: 'Review',
  components: {
    WrapLayout,
    VCard,
    FigureCardList,
    ProsConsList,
    TheSimpleNavBar
  },
  setup () {
    const baseInfo = computed(() => {
      return reviewData.baseInfo
    })

    const activeNames = ref([0])
    const evalInfo = computed(() => {
      return reviewData.eval
    })

    const adviceInfo = computed(() => {
      return reviewData.advice
    })

    let priceChart: echarts.ECharts | null = null
    const sizeState = ref(0)
    const sizeOption = reviewData.price.sizeOption

    const onDropdownChange = (value: number) => {
      console.log('value:', value)
      const option = createChartOption(reviewData.price.chartSource[value])
      priceChart && priceChart.setOption(option)
    }

    const onRendered = (index: number) => {
      if (index === 2) {
        const option = createChartOption(reviewData.price.chartSource[0])
        priceChart = echarts.init(document.getElementById('price-chart') as HTMLElement, undefined, { renderer: 'svg' })
        priceChart.setOption(option)
      }
    }

    const tableList = computed(() => reviewData.price.tableList)

    const figureGroupList = computed(() => reviewData.details.figureGroupList)
    return {
      baseInfo,
      evalInfo,
      activeNames,
      adviceInfo,
      sizeState,
      sizeOption,
      onDropdownChange,
      onRendered,
      tableList,
      figureGroupList
    }
  }
})
</script>

