
import { globalStyleKey } from '@/symbols/global'
import { defineComponent, inject } from 'vue'

export default defineComponent({
  props: {
    rowPadding: {
      type: Number
    }
  },
  setup (props) {
    const globalStyle = inject(globalStyleKey)
    if (globalStyle === undefined) {
      throw new Error('Failed to inject "globalStyle"')
    }
    const { rowPadding } = globalStyle

    return {
      wrapStyle: {
        paddingLeft: props.rowPadding ? props.rowPadding + 'px' : rowPadding + 'px',
        paddingRight: props.rowPadding ? props.rowPadding + 'px' : rowPadding + 'px'
      }
    }
  }
})
