<template>
  <h2 v-if="titleLevel === 2" class="truncate">
    <slot></slot>
  </h2>
  <h3 v-else-if="titleLevel === 3" class="truncate">
    <slot></slot>
  </h3>
  <h4 v-else-if="titleLevel === 4" class="truncate">
    <slot></slot>
  </h4>
  <h5 v-else-if="titleLevel === 5" class="truncate">
    <slot></slot>
  </h5>

</template>

<script lang="ts">
import { computed, defineComponent } from 'vue'

export default defineComponent({
  props: {
    level: {
      type: Number
    }
  },
  setup (props) {
    return {
      titleLevel: computed(() => props.level ? props.level : 2)
    }
  }
})
</script>

<style lang="stylus" scoped>
.truncate
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
h4
  font-size var(--ot-heading-16-size)
  line-height var(--ot-heading-16-line-height)
  font-weight bold
  margin 0
h5
  font-size var(--ot-heading-14-size)
  line-height var(--ot-heading-14-line-height)
  font-weight 500
