import { homePaths, sharePaths } from '@/router/router'
import { routeNamesEnum } from '@/router/routerEnum'
import { NavTabProps } from '@/store/store'
import { RouteRecordName } from 'vue-router'

const useGetTabbarActiveNameForPath = (path: string): RouteRecordName | null => {
  let activeName: RouteRecordName | null = null

  if (homePaths.includes(path)) {
    activeName = routeNamesEnum.Home
  } else if (sharePaths.includes(path)) {
    activeName = routeNamesEnum.Share
  }
  return activeName
}

export const useGetTabsActiveName = (tabs: Array<NavTabProps>, path: string): string => {
  const activeTab = tabs.find((tab: NavTabProps) => {
    return tab.path === path
  })
  return activeTab ? activeTab.name : ''
}

export const useGetTabsActivePath = (tabs: NavTabProps[], name: string): string => {
  const activeTab = tabs.find((tab: NavTabProps) => {
    return tab.name === name
  })
  return activeTab ? activeTab.path : ''
}

export default useGetTabbarActiveNameForPath
