<template>
  <section class="global-cont">
    <template v-for="(item, index) in attentionData" :key="index + 'i'">
      <v-full-card
        :header="{ avatar: item.avatar, author: item.author, authorIsV: item.authorIsV, moment: item.moment }"
        :cont="{ banner: item.banner, captions: item.captions }"
        :footer="{ comment: item.comment, good: item.good }"
        class="full-card"
      ></v-full-card>
    </template>
  </section>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from 'vue'
import { shareData } from '../mocks/localTest'
import VFullCard from '../components/VFullCard.vue'
import { AttentionProps } from '@/store/store'

export default defineComponent({
  name: 'ShareAttention',
  components: {
    VFullCard
  },
  setup () {
    const attentionData = ref<AttentionProps[]>([])
    function fetchData () {
      const data = shareData.attention(15)
      attentionData.value = [...attentionData.value, ...data]
    }
    onMounted(() => {
      fetchData()
    })
    return {
      attentionData
    }
  }
})
</script>

<style lang="stylus" scoped>
.full-card
  margin-bottom 10px
</style>
