import * as echarts from 'echarts/core'
import { LineChart, LineSeriesOption } from 'echarts/charts'
import { GridComponent, GridComponentOption, TitleComponent, TitleComponentOption, TooltipComponent, TooltipComponentOption, LegendComponent, LegendComponentOption, DatasetComponent, DatasetComponentOption } from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'

echarts.use([LineChart, GridComponent, TitleComponent, TooltipComponent, LegendComponent, CanvasRenderer, DatasetComponent])

type ECOption = echarts.ComposeOption<LineSeriesOption | GridComponentOption | TitleComponentOption | TooltipComponentOption | LegendComponentOption | DatasetComponentOption>

export const createChartOption = (source: Array<Array<string | number>>): ECOption => {
  const option: ECOption = {
    // 标题
    // title: {
    //   text: '价格趋势',
    //   textStyle: {
    //     fontSize: 14,
    //     fontWeight: 'normal',
    //     color: '#646566'
    //   },
    //   right: 10
    // },
    // 提示框
    tooltip: {
      trigger: 'axis'
    },
    // 图例选项
    legend: {
      right: 10,
      top: 8
      // selectedMode: 'single',
      // selected: {
      //   '48寸': true,
      //   '55寸': false,
      //   '65寸': false
      // },
      // data: ['48寸', '55寸', '65寸']
    },
    grid: {
      left: 50,
      right: 10
    },
    // 直角坐标系 grid 中的 x 轴
    xAxis: {
      type: 'category'
    },
    // 直角坐标系 grid 中的 y 轴
    yAxis: {
      scale: true
      // name: '￥'
      // boundaryGap: false
      // min: function (value: any) {
      //   return value.min - 300
      // },
      // max: function (value: any) {
      //   return value.max + 300
      // }
    },
    dataset: {
      source
    },
    series: [
      {
        // name: '48寸',
        type: 'line',
        seriesLayoutBy: 'row',
        step: 'end'
        // data: [3822, 3911, 3998, 3798, 3698, 3698, 3798],
        // dimensions: ['qwe', 'asd', 'zxc', 'rty', 'fgh', 'vbn', '123']
      },
      {
        // name: '55寸',
        type: 'line',
        seriesLayoutBy: 'row',
        step: 'end'
        // data: [4888, 4998, 4998, 4798, 4698, 4698, 4798]
      },
      {
        // name: '65寸',
        type: 'line',
        seriesLayoutBy: 'row',
        step: 'end'
        // data: [5222, 5498, 5498, 5598, 5198, 5198, 5598]
      }
    ]
  }
  return option
}

