<template>
  <nav class="nav-wrap">
    <div class="tabs">
      <van-tabs v-model:active="activeName" background="none" v-if="tabs" @change="onChange">
        <template v-for="tab in tabs" :key="tab.name">
          <van-tab :title="tab.title" :name="tab.name" :to="tab.path"></van-tab>
        </template>
      </van-tabs>
    </div>
    <div class="tools">
      <router-link to="/search"><van-icon name="search" size="20" class="icon"></van-icon></router-link>
      <van-icon name="user-circle-o" size="20" class="icon"></van-icon>
    </div>
  </nav>
</template>

<script lang="ts">
import { useGetTabsActiveName, useGetTabsActivePath } from '@/hooks/useGetActiveRoute'
import { StoreType, storeKey, NavTabProps } from '@/store/store'
import { computed, defineComponent, PropType, ref } from 'vue'
import { useRoute } from 'vue-router'
import { useStore } from 'vuex'

export default defineComponent({
  props: {
    tabs: {
      type: Array as PropType<NavTabProps[]>,
      required: true
    },
    viewType: {
      type: Number,
      required: true
    }
  },
  setup (props) {
    const store = useStore<StoreType>(storeKey)
    const route = useRoute()
    const activeName = ref(useGetTabsActiveName(props.tabs, route.fullPath))

    const onChange = (name: string) => {
      store.commit('setTabbarPath', { viewType: props.viewType, path: useGetTabsActivePath(props.tabs, name) })
    }
    return {
      activeName,
      onChange
    }
  }
})
</script>

