<template>
  <wrap-layout class="card-wrap">
    <h3>{{ category }}</h3>
    <div class="card-item" v-for="card in cardList" :key="card.id">
      <v-card :banner="card.banner" :captions="card.captions">
        <template #header>
          <div class="header">
            <van-tag v-if="card.score" size="medium" color="#43cf7c" type="primary" class="score">{{ card.score }}</van-tag>
            <span class="header-text">{{ card.reviewOption }}</span>
          </div>
        </template>
        <template #explain-top>
          <dl v-if="card.parameters" class="parameters">
            <template v-for="(parameter, index) in card.parameters" :key="'pa' + index">
              <dt>{{ parameter.label }}：</dt>
              <dd>{{ parameter.value }}</dd>
            </template>
          </dl>
        </template>
      </v-card>
    </div>
  </wrap-layout>
</template>

<script lang="ts">
import { computed, defineComponent, PropType } from 'vue'
import { FigureCardProps } from '../store/store'
import WrapLayout from '../layouts/wrapLayout.vue'
import VCard from './VCard.vue'

export default defineComponent({
  name: 'FigureCardList',
  components: {
    WrapLayout,
    VCard
  },
  props: {
    category: {
      type: String,
      required: true
    },
    list: {
      type: Array as PropType<FigureCardProps[]>,
      required: true
    }
  },
  setup (props) {
    const cardList = computed(() => props.list)
    return {
      cardList
    }
  }
})
</script>

