
import { defineComponent, onMounted, ref } from 'vue'
import { shareData } from '../mocks/localTest'
import VFullCard from '../components/VFullCard.vue'
import { AttentionProps } from '@/store/store'

export default defineComponent({
  name: 'ShareAttention',
  components: {
    VFullCard
  },
  setup () {
    const attentionData = ref<AttentionProps[]>([])
    function fetchData () {
      const data = shareData.attention(15)
      attentionData.value = [...attentionData.value, ...data]
    }
    onMounted(() => {
      fetchData()
    })
    return {
      attentionData
    }
  }
})
