<template>
  <wrap-layout class="group-list">
    <div v-for="group in groupList" :key="group.id" class="group-item">
      <div class="title">{{ group.title }}</div>
      <div class="tags-wrap">
        <template v-for="tag in group.tags" :key="tag.id">
          <router-link to="/top/best" class="tag-wrap">
            <van-tag size="large" plain class="tag">
              {{ tag.text }}
            </van-tag>
          </router-link>
        </template>
      </div>
    </div>
  </wrap-layout>
</template>

<script lang="ts">
import { computed, defineComponent, PropType } from 'vue'
import { TagGroupProps } from '../store/store'
import WrapLayout from '../layouts/wrapLayout.vue'

export default defineComponent({
  name: 'TagGroupList',
  components: {
    WrapLayout
  },
  props: {
    list: {
      type: Array as PropType<TagGroupProps[]>,
      require: true
    }

  },
  setup (props) {
    const groupList = computed(() => {
      return props.list ? props.list : []
    })
    return {
      groupList
    }
  }
})
</script>

